:root{
  --rosa-oscuro:#ED6E8D;
  --rosa-search:#eec0cc;
  --rosa-claro: #F2A3B8;
  --gris:#DCDCDC;
}

html, body
{
  margin:0;
  padding: 0;
}

:root {
  --rosa-oscuro: #ed6e8d;
  --rosa-claro: #f2a3b8;
  --rosa-search: #f8b8c9;
  --gris: #dcdcdc;
}

html,
body {
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}
:root {
  --rosa-claro: #f2a3b8;
  --rosa-oscuro: #ed6e8d;
  --blanco: white;

  --color-claro-buscador: rgba(255, 255, 255, 0.5);
  --color-fuente-buscador: white;
  --color-bordes: rgba(0, 0, 0, 0.1);
}

:root {
  --fuente-texto: "Roboto", sans-serif;
}

.H1 {
  text-align: center;
  font-family: "Roboto", sans-serif;
  font-family: var(--fuente-texto);
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
  border-bottom: 2px solid var(--color-bordes);
}

.H4 {
  font-family: "Roboto", sans-serif;
  font-family: var(--fuente-texto);
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
  border-bottom: 2px solid var(--color-bordes);
}

*{
  box-sizing: border-box;
}


header {
  display: flex;
  align-items: center;
	justify-content: center;
	background-color: var(--rosa-claro);
	border-bottom: 1px solid white; 
  height: 60px;
}
  
  #cajaBuscador {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80%;
    height: 50%;
    border-bottom: 1px solid white;
  }
  
  #buscar::-webkit-input-placeholder {
    color: var(--color-claro-buscador);
  }
  
  #buscar:-ms-input-placeholder {
    color: var(--color-claro-buscador);
  }
  
  #buscar::placeholder {
    color: var(--color-claro-buscador);
  }
  #buscar {
    flex-grow: 1; 
    background-color: var(--rosa-claro);
    outline-style: none;
    font-size: 16px;
    font-weight: bold;
    border: none;
    color: white;
}

  #IconoBuscador{
   color: white;
  }

  #lupa {
  }
footer
{
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: var(--fuente-texto);
    position: fixed;
    bottom: 0vh;
    background-color: var(--rosa-claro);
    width: 100%;
    height: 60px;
}

#IconoFooter{
    color: white;

}

